.MovieInfo {
    position: fixed;
    background: #393e46;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    color: #eeeeee;
    padding: 25px;
}

.close {
    position: absolute;
    top: 25px;
    right: 25px;
    font-size: 35px;
    cursor: pointer;
    transition: .2s;    
}

.close:hover {
    color: #00adb5;
}

.firstBlock{
    width: 100%;
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-column-gap: 90px;
}

.poster {
    width: 100%;
    border-radius: 15px;
    margin-bottom: 15px;
}

.wrapper{
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
}

h1{
    font-size: 40px;
    padding-right: 35px;
}

.reslease{
    color: #00adb5;
    font-size: 23px;
    margin: 15px 0 15px 0 ;
}

.overview{
    font-size: 20px;
}

.ganre{
    margin-top: 5px;
    color: #d6c211;
}

.production{
    margin-top: 10px;
    font-size: 20px;
}

.word {
    color: #f05454;
    font-weight: normal;
}

.posterWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

a{
    text-decoration: none;
    padding: 18px 100px;
    background: #b02725;
    margin: 0 auto;
    color: #eeeeee;
    border-radius: 15px;
    transition: .2s;
}

a:hover {
    background: #99110f;
}